/**
 * 	Keeps track of the URLs (including redirects) that have been navigated to by the user.
 * 	Once the user navigates to a completely different URL, the history is cleared.
 *
 * 	For example, lets say you have the URL http://example.org/foo, which redirects to
 * 	http://example.org/foo/bar and then http://somewhere-else.com, the stack will look like this:
 *
 * 	[
 * 		"http://example.org/foo",
 * 		"http://example.org/foo/bar",
 * 		"http://somewhere-else.com"
 * 	]
 *
 *  However, if http://example.org/foo didn't perform any redirects, the stack will look like this:
 *
 *  [
 * 		"http://example.org/foo"
 * 	]
 *
 * 	It's driven by the webRequest.beforeRequest + webNavigation.onCommitted events.
 *
 * 	@author Daniel Moffat
 */

var URI = require('urijs');
var {isClickthrough, isGiveasyouLiveEmail} = require('./shared/util.js');

function NavigationTracker() {
	// The current stack of URLs that've been navigated to
	// todo: rename, this is a bad name, it's actually a list of NavigationTrackerItem's, rather than URLs as the name implies...
	this.urls = [];

	// The last stack of URLs that've been navigated to
	this.oldUrls = [];
}

/**
 * Adds a URL to the navigation stack, extracting metadata from the URL, such as the host and the domain.
 */
NavigationTracker.prototype.push = function(url) {
	this.urls.push(new NavigationTrackerItem(url));
};

/**
 * @return The last URL that in the navigation stack, or null if there isn't one (this usually happens if the page is refreshed, or typed directly into the address bar.)
 */
NavigationTracker.prototype.last = function() {
	return this.urls[this.urls.length - 1] || null;
};

/**
 * @return the first URL in the navigation stack (i.e the first one the user navigated to)
 */
NavigationTracker.prototype.first = function() {
	return this.urls[0] || null;
};

/**
 * @param  urlOnly Whether to return just URLs, or all metadata.
 * @return A list of navigation items (which contain URL metadata), or just a list of URLs, depending on the value of the urlOnly flag.
 */
NavigationTracker.prototype.currentUrls = function(urlOnly) {
	if(urlOnly) {
		return this.urls.map(function(trackerItem) { return trackerItem.url });
	}
	return this.urls;
};

/**
 * Clears the navigation stack, and keeps a reference to the old navigation stack.
 */
NavigationTracker.prototype.clear = function() {
	this.oldUrls = this.urls;
	this.urls = [];
};

/**
 * @return True if the navigation stack is empty.
 */
NavigationTracker.prototype.isEmpty = function() {
	return this.urls.length === 0;
};

/**
 * @return True if the navigation stack was the result of a direct click from either the GAYL.com website, or
 * an email.
 */
NavigationTracker.prototype.wasDirectClick = function() {
	return this.firstWasClickthrough() || // If the first URL was the clickthrough, they've clicked directly from the GAYL website
			(this.firstWasEmail() && this.wasClickthrough(1)); // Ensure the navigation is email click -> clickthrough before turning it on.
};

/**
 * @return True if the first URL in the navigation stack is the clickthrough URL.
 */
NavigationTracker.prototype.firstWasClickthrough = function() {
	var first = this.first();
	if(first && first.url && isClickthrough(first.url)) {
		return true;
	}
	return false;
};

/**
 * @return True if the given index in the navigation stack is a clickthrough URL.
 */
NavigationTracker.prototype.wasClickthrough = function(idx) {
	if(idx >= this.urls.length) { // Make sure the index is accessible
		return false;
	}

	var item = this.urls[idx];

	return isClickthrough(item.url);
};

/**
 * @return True if the navigation stack's first URL was from a Give as you Live email.
 */
NavigationTracker.prototype.firstWasEmail = function() {
	var first = this.first();
	if(first && first.url && isGiveasyouLiveEmail(first.url)) {
		return true;
	}
	return false;
};

/**
 * @return True if the given url is in the navigation stack
 */
NavigationTracker.prototype.contains = function(url) {
	for (var i = 0; i < this.urls.length; i++) {
		var item = this.urls[i];
		if(item.url === url) {
			return true;
		}
	}
	return false;
};

/**
 * Logs all URLs that are in the navigation stack
 */
NavigationTracker.prototype.log = function() {
	console.log('Navigation URLS:');
	this.currentUrls(true).forEach(function(url) {
		console.log('- ' + url);
	});
};

/**
 * Represents a URL that has been navigated through. Extracts additional metadata from the URL for use later on.
 */
function NavigationTrackerItem(url) {
	var uri = URI(url);
	this.url = url;
	this.domain = uri.domain();
	this.host = uri.host();
}

module.exports = NavigationTracker;