/**
 * 	This is the core class of the extension. It listens to browser events to determine if a retailer is supported, and events
 * 	sent from other extension components.
 *
 * 	When a user browses the internet, it collects URLs that have been visited, and sends them off to our REST API to determine
 * 	if the current website is a supported retailer or not.
 *
 * 	It is used by the following extension components:
 *	- Content scripts, to display retailer alerts
 *	- Popup, to obtain information about the currently logged in user, and the current website's state.
 *
 * 	@author Daniel Moffat
 */

var URI = require('urijs');

var { browserAction, runtime, webRequest, webNavigation, windows } = require('./web-ext/index.js');
var { createError, isClickthrough, isMajorVersionUpdate, isSerp, isUndefined, logObject } = require('./shared/util.js');
var { BrowserTab, BrowserTabs } = require('./browser-tabs.js');
var { Message, MessageSubject } = require('./shared/message.js');
var Api = require('./gayl-api-client.js');
var checkPermissions = require('./shared/check-permissions.js');
var Config = require('./config.development.js');
var getBrowserName = require('./shared/get-browser-name.js');
var setBrowserIcon = require('./shared/set-browser-icon.js');
var Storage = require('./storage.js');
var StoreStatus = require('./shared/store-status.js');
var tabs = require('./web-ext/tabs.js');
var visitClickthrough = require('./shared/visit-clickthrough.js');

class ExtensionCore {
	constructor() {
		// Holds information about the browser tabs and their state
		this.tabs = new BrowserTabs();

		// Contains the currently logged in user.
		this.user = null;

		// Contains the currently logged in user's cause.
		this.cause = null;

		// Contains the currently logged in user's device security code, this is just a token that's returned after a user authenticates.
		this.deviceSecurityCode = null;

		// For making GAYL API requests
		this.api = Api;

		// For persistent storage
		this.storage = Storage;

		// Listen for browser events
		this.bindEventHandlers();

		this.setUninstallUrl(Config.uninstallUrl);
	}

	bindEventHandlers() {

		/**
		 * 	Make Safari's popup behave the same way as the other browsers (full reload each time it's opened.),
		 * 	this makes it much easier to work with, as you don't need to worry about what state the popup was in
		 * 	when it was closed.
		 */
		if('undefined' !== typeof safari) {
			safari.application.addEventListener('command', this.onClickSafariExtensionIcon.bind(this), false);
		}

		/**
		 * Invoked before every HTTP request.
		 *
		 * This is executed every time a HTTP request is made for top-level documents (not iFrames).
		 */
		webRequest.onBeforeRequest.addListener(this.onBeforeRequest.bind(this), {
			urls: ['http://*/*', 'https://*/*'], // Only run on certain schemes, otherwise you get junk from chrome-extension://
			types: ['main_frame'] // Limit to top-level documents
		});

		// NOTE: This having an empty function is INTENDED, due to this bug in Firefox (https://bugzilla.mozilla.org/show_bug.cgi?id=1429556),
		// without it, redirected URLs are not picked up inside webRequest.onBeforeRequest().
		webRequest.onBeforeRedirect.addListener(function() {}, { urls: ['<all_urls>'] });

		/**
		 * Invoked when a new tab is created. Note, this is not invoked when a pre-rendered tab is created...
		 *
		 * docs: https://developer.mozilla.org/en-US/Add-ons/WebExtensions/API/tabs/onCreated
		 */
		tabs.onCreated.addListener(this.onTabCreated.bind(this));

		/**
		 * Invoked when a tab is removed.
		 *
		 * docs: https://developer.mozilla.org/en-US/Add-ons/WebExtensions/API/tabs/onRemoved
		 */
		tabs.onRemoved.addListener(this.onTabRemoved.bind(this));

		/**
		 * 	Invoked when the active tab in a window changes.
		 *
		 * 	docs: https://developer.mozilla.org/en-US/Add-ons/WebExtensions/API/tabs/onActivated
		 */
		tabs.onActivated.addListener(this.onTabActivated.bind(this));

		/**
		 * 	Invoked when the currently focused window changes.
		 *
		 * 	docs: https://developer.mozilla.org/en-US/Add-ons/WebExtensions/API/windows/onFocusChanged
		 */
		windows.onFocusChanged.addListener(this.onWindowFocusChanged.bind(this));	

		/**
		 *	Invoked when a message is receieved from a content script / the popup.
		 *
		 * 	To send a message which will be received by the onMessage listener, use runtime.sendMessage() or (to send a message to a content script) tabs.sendMessage().
		 *
		 * 	docs: https://developer.mozilla.org/en-US/Add-ons/WebExtensions/API/runtime/onMessage
		 */
		runtime.onMessage.addListener(this.onMessage.bind(this));

		/**
		 *  Explanation of this: 'webNavigation.onCommitted' doesn't get fired in Safari when you type into the address bar and press enter - it gets fired
		 *  as you're typing and doesn't get fired again when you press enter. As a result the request to check the current
		 *  website never gets sent until the page is refreshed. 'tabs.onUpdated' seems to get fired when I want it to (after you press enter), but I haven't
		 *  had time to test this in all the other browsers, so for now this remains a Safari-specific fix.
		 */
		if(getBrowserName() === "Safari") {
			tabs.onUpdated.addListener(this.onTabUpdated.bind(this));
		} else {
			/**
			 * Invoked when a navigation is committed. It's committed when part of the new document
			 * has been received from the server and the browser has decided to switch to the new document.
			 *
			 * docs: https://developer.mozilla.org/en-US/Add-ons/WebExtensions/API/webNavigation/onCommitted
			 */
			webNavigation.onCommitted.addListener(this.onNavigationCommitted.bind(this));
		}
	}

	onTabUpdated(tabId, changeInfo, tabInfo) {
		// There are lots of onTabUpdated events that are fired, during the loading a page, the last one has a status of 'complete'.
		// It's also fired with an empty 'url' property when a new tab is created.
		if(changeInfo.status === 'complete' && tabInfo.url !== '') {
			console.log('onTabUpdated()', changeInfo, tabInfo);
			// See comment in 'bindEventHandlers' where this gets bound to see why this function is calling onNavigationCommitted.
			this.onNavigationCommitted({
				frameId: 0,
				tabId: tabId,
				url: tabInfo.url
			});	
		}
	}

	onTabReplaced(addedTabId, removedTabId) {
		console.log(`Added tab ID: ${addedTabId}, removed tab ID: ${removedTabId}`);
	}

	onClickSafariExtensionIcon(e) {
		if(e.command === 'open-popover') {
			e.target.popover.contentWindow.location.reload();
			e.target.showPopover();
		}
	}

	onBeforeRequest(requestDetails) {
		var { tabId, url, parentFrameId } = requestDetails;

		// See method definition for an explanation of why this is needed.
		if(this.shouldIgnoreRequestSafari(parentFrameId, url)) {
			console.log('Ignoring: ', url);
			console.log('Parent frame id was: ', parentFrameId);
			return;
		}

		if(!isSerp(requestDetails.url)) {
			// Get a handle on the tab that caused this event to fire. This may be a tab which is pre-rendered and not visible, so we need
			// to check if it actually exists in our tab map.
			var tab = this.tabs.find(tabId);
			if(!isUndefined(tab)) {
				console.log('Adding navigated URL: ', url);
				tab.navigation.push(requestDetails.url);
			}
		}
	}

	// Safari's implementation of the 'webRequest' API doesn't support the 'opt_extraInfoSpec' parameter, meaning we can't filter out
	// requests when registering an event handler. To get around this we filter those requests inside the handler itself on Safari only.
	shouldIgnoreRequestSafari(parentFrameId, url) {
		if(getBrowserName() !== 'Safari') {
			return false;
		}
		return !url.startsWith('http') || parentFrameId !== 0;
	}

	onTabCreated(tabInfo) {
		console.log('onTabCreated(' + tabInfo.id + ')');
		// Workaround a bug that affects Safari only when the browser is opened for the first time.
		if('undefined' !== typeof safari) {
			// If there's no active tab, set it to the tab that was just created. This only happens
			// when Safari is opened for the first time and no tabs are discovered.
			if(!this.tabs.activeTab) {
				this.tabs.setActiveTab(tabInfo.id);
			} else {
				// If there's already an active tab, just add the recently created one to the list.
				// We can't set it to active, because it may have been created in the background via CMD+click.
				this.tabs.add(tabInfo.id);
			}
		} else {
			// Workaround for a bug in Firefox 48, where the onTabActivated event is fired before the onTabCreated event.
			// What happens:
			// - onTabActivated fired -> tab is set to active and added to internal tab map if it doesn't exist
			// - onTabCreated fired -> tab is added to internal tab map, overwriting the existing tab state
			// In other browsers these events fire in the order onTabCreated -> onTabActivated
			if(!this.tabs.find(tabInfo.id)) {
				this.tabs.add(tabInfo.id);
			} else {
				console.log('Firefox 48 BUG.');
			}
		}
	}

	onTabRemoved(tabId, removeInfo) {
		console.log('onTabRemoved(tabId=' + tabId + ')');
		this.tabs.remove(tabId);
	}

	onTabActivated(tabInfo) {
		console.log('onTabActivated(tabId=' + tabInfo.tabId + ')');
		this.tabs.setActiveTab(tabInfo.tabId);
		this.updateIcon();
	}

	onWindowFocusChanged(windowId) {
		console.log('onWindowFocusChanged(windowId=' + windowId + ')');
		var self = this;
		// Find the active tab in the window that was switched to.
		tabs.customQuery({
			currentWindow: true,
			active: true
		}).then(function(tabs) {
			if(!tabs || !tabs.length) { // Clear the active tab if none are in this window (e.g. A detatched devtools window.)
				self.tabs.setActiveTab(-1);
			} else {
				self.tabs.setActiveTab(tabs[0].id);
				self.updateIcon();
			}
		});
	}

	/**
	 * This is called before executing any of the logic in the onNavigationCommitted event handler. Its aim
	 * is to filter out URLs we know we can't process.
	 *
	 * @return True if the given URL / frame ID should be processed.
	 */
	shouldProcessUrl(url, frameId) {
		return !isBlacklisted(url) &&
				!isClickthrough(url) &&
				!isGoogleChromeNewTab(url) &&
				!isIgnoredScheme(url) &&
				isCurrentFrame(frameId);
	}

	/**
	 * 	Invoked when the user navigates to a URL. This can be fired multiple times per page load and for some browsers
	 * 	will even be fired for pre-rendered tabs that aren't visible to the end-user.
	 *
	 *  It will also be fired twice in the same second for websites which perform client-side redirects.
	 *
	 *  @param request Contains detailed information about the current request.
	 */
	onNavigationCommitted(request) {
		var self = this;

		// An initial check before doing any processing to see if we can actually process this URL.
		if(!this.shouldProcessUrl(request.url, request.frameId)) {
			return;
		}

		// Check is the user has given the extension permission.
		checkPermissions(function(hasPermission) {
			if(!hasPermission) { // Can't do anything without permission.
				return;
			}

			// Next, match the tabID in the request, with our in-memory list of tabs.
			var tab = self.tabs.find(request.tabId);

			// If no tab was found, it can mean a few things:
			// 1) The tab is being pre-rendered by Chrome (or other browsers which do this)
			// 2) It's a new tab in Safari
			if(!tab) {
				console.log('### Pre-render / new Safari tab / tab removal detected. ###');
				return;
			}

			// Finally, process the URL. This executes at most once per 'Config.processingDelayMs'. The main purpose of
			// 'executeOnce' is to handle multiple onNavigationCommitted callbacks being invoked in a short space of time.
			tab.executeOnce(function() {
				// Take a copy of the current URL and the new URL
				var oldUrl = tab.url;
				var newUrl = request.url;

				// Update the tab's URL with the one we're navigating to.
				tab.url = newUrl;

				console.log('onNavigationCommitted - ' + newUrl + ' (tabId=' + request.tabId + ')');
				console.log(oldUrl + ' -> ' + newUrl);

				// If the navigation stack is empty, the current tab has just been reloaded.
				if(tab.navigation.isEmpty()) {
					console.log('Navigation stack is empty, possible page reload / direct navigation.');
					tab.navigation.push(newUrl);
				}

				// Fixes a bug where a website is loaded through a Service Worker (https://developers.google.com/web/fundamentals/primers/service-workers/).
				// In this case, an onBeforeRequest event is emitted with a type of 'xmlhttprequest', rather than 'main_frame' resulting in the URL not being added
				// to the list of navigated URLs.
				if(!tab.navigation.contains(newUrl)) {
					tab.navigation.push(newUrl);
				}

				tab.navigation.log();

				// Now, check user state on GAYL and see if the current store is supported.
				// If any error occurs during either of these 2 steps, this.onError will be invoked.
				self.getAndUpdateUserState(request.tabId)
					.then(function(data){
						console.log('2. Checking if retailer is supported. (tabId=' + request.tabId + ')');
						return self.checkRetailerSupported.bind(self)(data, tab.id);
					})
					.then(function(findStoreByUrlResponse) {
						console.log('3. Analysing API response. (tabId=' + request.tabId + ')');
						return self.handleRetailerResponse.bind(self)(findStoreByUrlResponse, request.tabId);
					})
					.catch(self.onError.bind(self))
					.then(function() { // Should be .finally, but that's not supported in Safari.
						console.log('4. Finished checking retailer. (tabId=' + request.tabId + ')');
						self.updateIcon();

						// Clear the navigated URLs once the API request has finished, check the tab exists incase it gets removed from the tab map.
						if(tab) {
							tab.navigation.clear();
						}
					});
			});
		});
	}

	/**
	 * Retreives the user's state on GAYL.com and either persists it (if they're logged in) or clears it (if they're logged out).
	 * The reason it clears it if they're logged out is that they may have logged out of the website, and the extension needs to keep
	 * in sync with the website.
	 */
	getAndUpdateUserState(tabId) {
		var self = this;
		console.log('1. Checking user GAYL.com state. (tabId=' + tabId + ')');
		return this.api.getState({useCache: true}).then(function(resp) {
			if(resp.user.loggedIn) {
				return self.persistState(resp);
			} else {
				// If they aren't logged in, clear any existing state that might be cached / in-memory
				return self.clearUserState();
			}
		});
	}

	/**
	 * Persists user state in the persistent storage and Sentry context.
	 */
	persistState(getStateResponse) {
		// Save them in memory so we can access them using this.XXX
		this.user = getStateResponse.user;
		this.cause = getStateResponse.cause;
		this.deviceSecurityCode = getStateResponse.deviceSecurityCode;

		// Add them to persistent storage too.
		return this.storage.set({
			'deviceSecurityCode': getStateResponse.deviceSecurityCode,
			'user': getStateResponse.user,
			'cause': getStateResponse.cause
		});
	}

	/**
	 * Clears all user state from the extension, including the persistent storage, the background page and the Sentry context.
	 */
	clearUserState() {
		this.storage.clear();
		this.user = null;
		this.cause = null;
		this.deviceSecurityCode = null;
	}

	/**
	 * This method is invoked when an error occurs during the retailer checking process, possible errors are:
	 * - Request timeout or request cancelled (http_error_timeout_or_cancelled)
	 * - API error (api_error), malformed request was sent to the server
	 * - Tab deleted (tab_deleted), tab was closed whilst it was being processed.
	 * - No navigated URLs (no_navigated_urls), no URLs were navigated through to get to this page.
	 * If it's not one of these errors, it will be recorded to Sentry as a bug.
	 */
	onError(err) {
		// Map of error types + their handler functions - currently they don't do anything, but they aren't fatal
		// errors. The extension still functions just fine - the errors were thrown to exit early from a promise chain.
		// In some situations we can't actually recover anyway, without somehow invoking onNavigationCommitted again to
		// retry.
		var errorFns = {
			'api_error': function() {
				console.warn('API returned 4xx or 5xx');
			},
			'http_error_timeout_or_cancelled': function() {
				console.warn('HTTP error - request execeeded ' + Config.requestTimeoutMs + 'ms OR cancelled by browser.');
			},
			'tab_deleted': function() {
				console.warn('Tab was deleted whilst being processed.');
			},
			'no_navigated_urls': function() {
				console.warn('No navigated URLs - possible overlap');
			}
		};

		var errorFn = errorFns[err.type];

		// If an error handling function was defined, invoke it.
		if(!isUndefined(errorFn)) {
			errorFn();
		} else {
			console.error(err);
		}
	}

	// Given a findStoreByUrlResponse, this method will return the store found in that response,
	// based on some rules...
	//
	// The rules are a bit odd, so I'll try to explain:
	// 1. If there's 1 store in the response, it means it's supported and will be returned.
	// 2. If there's more than one store, it means this store is only supported on specific paths
	// 		a) If the path matches the path of the current website, it will be returned.
	// 		b) If there are no matches, null will be returned.
	extractStore(findStoreByUrlResponse, currentTab) {
		var stores = findStoreByUrlResponse.list;

		// 1.
		if(stores.length === 1) {
			return stores[0];
		}

		// 2.
		// See if the paths match
		var currentTabUrl = currentTab.navigation.last().url;

		for (var i = 0; i < stores.length; i++) {
			var store = stores[i];

			if(currentTabUrl.indexOf(store.folder) > -1) {
				return store;
			}
		}

		// No matched path
		return null;
	}

	/**
	 *	This is the main function that determines if the current website
	 *	is a supported retailer, and if we are allowed to inject our dialogs.
	 *	Data is an object with the following keys:
	 *	- user, user info
	 *	- cause, user's cause info
	 *	- deviceSecurityCode
	 */
	checkRetailerSupported(data, tabId) {
		var currentTab = this.tabs.find(tabId);
		if(!currentTab) {
			console.warn(tabId + ' not found before calling supported API.');
			return Promise.reject(createError('tab_deleted'));
		}

		// Obtain the URLs the user navigated through.
		var navigatedUrls = currentTab.navigation.currentUrls(true);

		if(this.isLoggedIn()) {
			return this.api.findStoreByUrl(data.deviceSecurityCode, navigatedUrls);
		} else {
			return this.api.findStoreByUrlAnonymous(navigatedUrls);
		}
	}

	/**
	 * 	This method is called when this.checkRetailerSupported has finished executing.
	 *  It can be called multiple times for one website, for example:
	 *  - User visits hobbs.co.uk <-- called once here
	 *  - User clicks "Yes" on confirmation dialog, this sends them to the GAYL clickthrough
	 *  - Clickthrough redirects user back to the hobbs.co.uk
	 *  - onNavigationCommitted is fired again <-- called again here
	 */
	handleRetailerResponse(findStoreByUrlResponse, tabId) {
		// Get a handle on the current tab, this may differ from the active tab if the user changes tabs whilst
		// this is being executed.
		var currentTab = this.tabs.find(tabId);

		// If we couldn't get a handle on the current tab, we can't do anything as the tab doesn't exist / have state.
		if(!currentTab) {
			console.warn(tabId + ' not found when handling retailer response.');
			return Promise.reject(createError("tab_deleted"));
		}

		// Retailer wasn't found in the API
		if(!findStoreByUrlResponse.found) {
			return this.handleRetailerNotSupported(currentTab);
		}

		// Extract the store from the response, see extractStore for more comments.
		var store = this.extractStore(findStoreByUrlResponse, currentTab);
		if(store === null) {
			return this.handleRetailerNotSupported(currentTab);
		}

		// Now the store is extracted from the response, check the existing tabs we have to see if
		// any of them have activated this store before. If they have, we can copy the tracking state across
		// so that it remains activated. There's no need to copy the state if the tracking status is BLANK.
		var existingTabWithStore = this.tabs.findByStoreId(store.id, tabId);
		if(existingTabWithStore) {
			console.log('Store already exists in another tab.');
			console.log('Current tab status: ', currentTab.currentStatus());
			console.log('Existing tab status: ', existingTabWithStore.currentStatus())
			BrowserTab.copy(existingTabWithStore, currentTab);
		}

		 // Check if the current store stored for this tab matches the store we found in the API.
		 // If it doesn't match, it means the user has navigated to a new retailer, and we need to reset the
		 // tracking status.
		if(!currentTab.isCurrentStore(store)) {
			console.log('User transitioned to a different store, resetting the state...');
			currentTab.status = StoreStatus.BLANK;
		}

		// Save the store from the API against the currently active tab.
		currentTab.currentStore = store;

		// If the navigated URLs look like a direct click (from either the GAYL website, or an email), we can handle it the same as if they clicked "Yes" on the confirmation dialog.
		if(currentTab.navigation.wasDirectClick()) {
			// Temporary fix to resolve a bug where findStoreByUrl caches an API response which changes between requests.
			// It only occurs when you activate a retailer which doesn't support the toolbar, since the standDown flag gets set to false temporarily
			// Allow toolbar retailer: standDown = false, allowToolbar = true, standDown gets set to false
			// Disallow toolbar retailer: standDown = false (when first URL was the clickthrough), allowToolbar = false, standDown gets set to true.
			// todo: remove this when I rewrite the way the current store gets looked up.
			console.log('Changing cached findStoreByUrl response... from ' + findStoreByUrlResponse.standDown + ' to ' + !currentTab.currentStore.allowToolbar);
			findStoreByUrlResponse.standDown = !currentTab.currentStore.allowToolbar;

			this.handleStatusTracking(currentTab);
		}

		// Finally perform some additional actions based on the status of this store.
		switch(currentTab.currentStatus()) {
			// Store is already tracked
			case StoreStatus.TRACKED:
				// Do nothing if it's already tracking
				return;
			// User has clicked "Yes" on the confirmation dialog, has been redirected to the GAYL
			// clickthrough, which has sent them back to the retailer page, which invoked this method again.
			case StoreStatus.TRACKING:
				return this.handleStatusTracking(currentTab);
			// User has clicked "No" on the confirmation dialog
			case StoreStatus.NOT_SUPPORTED:
				return Promise.resolve();
			// User hasn't actioned this store yet.
			case StoreStatus.BLANK:
				return this.handleStatusBlank(findStoreByUrlResponse, currentTab);
			default:
				return this.handleUnknownStatus();
		}
	}

	/**
	 * Invoked when the status of the current tab is "TRACKING". The tab is in this state when the user has clicked "YES" on the confirmation dialog.
	 */
	handleStatusTracking(currentTab) {
		// Activate the current tab.
		console.log('This store is in the process of being tracked.');
		currentTab.activeStore = currentTab.currentStore;
		currentTab.status = StoreStatus.TRACKED; // Change the status so the user doesn't see the thank you dialog again.
		this.showThankYou(currentTab);

		// Activate any other tabs which contain this store.
		// todo: combine this with the above and do all tabs in one go
		console.log('Activating all other tabs...');
		this.tabs.updateTabsWithMatchingStore(currentTab);
		var tabs = this.tabs.findAllByStoreId(currentTab.currentStore.id, currentTab.id);
		console.log('Found this store in ' + tabs.length + ' other tabs.');
		this.showThankYouForTabs(tabs);

		return Promise.resolve();
	}

	/**
	 * Invoked when the status of the current tab is "BLANK".
	 */
	handleStatusBlank(findStoreByUrlResponse, currentTab) {
		// Are we allowed to show the user the dialog?
		// todo: This should be moved out of here and next to the .found check in handleRetailerResponse.
		if(findStoreByUrlResponse.standDown) {
			console.log('Standing down...');
			return;
		}

		// Yay! We're allowed to show the confirmation dialog
		// Note: The user's response to this dialog is handled in onMessage() - MessageSubject.RETAILER_CONFIRM and
		// MessageSubject.RETAILER_REJECT
		this.showConfirmationDialog(currentTab.id, Object.assign({}, findStoreByUrlResponse.list[0]));

		return Promise.resolve();
	}

	/**
	 * Invoked inside handleRetailerResponse when the retailer was not found in our API.
	 */
	handleRetailerNotSupported(tab) {
		tab.status = StoreStatus.BLANK;
		tab.currentStore = {};
		return Promise.resolve();
	}

	/**
	 * Invoked when the status of the current tab doesn't match our list of defined states.
	 */
	handleUnknownStatus() {
		return Promise.reject(new Error("Unknown status: " + activeTab.currentStatus()));
	}

	/**
	 * Sends a message to the content script in the given tab ID, instructing it to show the confirmation dialog.
	 */
	showConfirmationDialog(tabId, store) {
		var message = new Message(MessageSubject.RETAILER_CONFIRMATION, {
			cause: this.cause,
			store: store
		});
		this.sendMessageToContentScript(tabId, message);
	}

	/**
	 * Sends a message to the content script in the given tab ID, instructing it to hide the confirmation dialog.
	 */
	hideConfirmationDialog(tabId) {
		this.sendMessageToContentScript(tabId, new Message(MessageSubject.HIDE_CONFIRMATION));
	}

	/**
	 * Sends a message to the content script for each tab in the list, instructing it to hide the confirmation dialog.
	 */
	hideConfirmationDialogForTabs(tabs) {
		var self = this;
		tabs.forEach(function(tab) {
			self.hideConfirmationDialog(tab.id);
		});
	}

	/**
	 * Sends a message to the content script in the given tab, instructing it to show the thank you message.
	 */
	showThankYou(tab) {
		var message = new Message(MessageSubject.RETAILER_THANKYOU, {
			cause: this.cause,
			store: tab.activeStore
		});
		this.sendMessageToContentScript(tab.id, message);
	}

	/**
	 * Sends a message to the content script for each tab in the list, instructing it to show the thank you message.
	 */
	showThankYouForTabs(tabs) {
		var self = this;
		tabs.forEach(function(tab) {
			self.showThankYou(tab);
		});
	}

	/**
	 * Sends a message to the given tab ID
	 */
	sendMessageToContentScript(tabId, message) {
		console.log('Sending content script message to tab(' + tabId + ') with message: subject=' +  message.subject + ' payload=' + logObject(message.payload));
		if(tabId) { // May be undefined if there is no active tab / tab reference
			tabs.sendMessage(tabId, message, function() {
				// This callback stops this error being thrown - "The message port closed before a response was received.". 
				// From a quick bit of reading I'm not doing the async message passing correctly. I will revisit this as part of Manifest v3 refactoring.
				// An exception is thrown if this property if not checked in this callback, even if you decided to do nothing with it.
				if(runtime.lastError) {} else {}
			})
		}
	}

	/**
	 * @return True if the user is logged in
	 */
	isLoggedIn() {
		return this.user && this.user.loggedIn;
	}

	/**
	 * Called when the background page receives a message from a content script.
	 */
	onMessage(message, sender, sendResponse) {
		if(!isTrusted(sender)) {
			console.log('Received an untrusted message from ' + sender.id);
			return; // Only respond to messages sent by our extension
		}
		console.log('Background received message: ' + message.subject + ' with payload ' + logObject(message.payload));
		var { payload, subject } = message;
		var activeTab = this.tabs.activeTab;
		var self = this;

		// Try and invoke the message handler, if an exception occurs it will be logged to Sentry.
		try {
			switch(subject) {
				// User has said "yes", they'd like to track their purchases.
				case MessageSubject.RETAILER_CONFIRM:
					this.onConfirmation(payload.extensionSource);
					break;
				// User doesn't want their purchases tracked.
				case MessageSubject.RETAILER_REJECT:
					this.onRejection();
					break;
				// Content script has requested the SERP extraction rules.
				case MessageSubject.GET_SERP_RULES:
					this.getSerpRules();
					break;
				// Content script has requested its extracted URLs be checked.
				case MessageSubject.CHECK_SERP_URLS:
					console.log('Check serp URLS')
					this.checkSerpUrls(payload);
					break;
				default:
					console.warn('Unable to handle event - ' + subject);
					break;
			}
		} catch(e) {
			console.error(e);
		}
		return true;
	}

	/**
	 * Fetches the SERP extraction rules from the API. These dictate how URLs should be extracted from SERPs.
	 */
	getSerpRules() {
		var activeTab = this.tabs.activeTab;
		var self = this;
		this.api.getSerpRules().then(function(rules) {
			// todo: Will activeTab be sufficient? Or will we need the TAB ID that sent the message incase tab gets switched.
			self.sendMessageToContentScript(activeTab.id, new Message(MessageSubject.GET_SERP_RULES_RESPONSE, rules));
			console.log('API request finished: ', rules);
		}).catch(function() {}); // If we failed to fetch the rules there's nothing we can do. This prevents uncaught rejected promises being logged in Sentry.
	}

	/**
	 * Checks the URLs in the payload to determine if they are supported by the GAYL website.
	 *
	 * Payload is an object in the format:
	 * { urls: ['https://www.hmv.com', 'https://www.google.com', ...] }
	 */
	checkSerpUrls(payload) {
		var activeTab = this.tabs.activeTab;
		var self = this;
		console.log('Checking SERP URLs')
		this.api.checkSerpUrls(payload).then(function(resp) {
			self.sendMessageToContentScript(activeTab.id, new Message(MessageSubject.CHECK_SERP_URLS_RESPONSE, resp));
			console.log('API request finished', resp);
		}).catch(function() {}); // // If we failed to check the URLs there's nothing we can do. This prevents uncaught rejected promises being logged in Sentry.
	}

	/**
	 * Invoked when the user agrees to turn on the Donation Reminder.
	 * This follows the clickthrough URL for this retailer, and also activates any other tabs with this retailer in.
	 * @param source Where this click came from, possible values live inside ./shared/extension-source.js
	 */
	onConfirmation(source) {
		var activeTab = this.tabs.activeTab;
		activeTab.activeStore = activeTab.currentStore;
		activeTab.status = StoreStatus.TRACKING;
		console.log('User has confirmed the retailer - ' + logObject(activeTab.activeStore));
		console.log('They were on the URL ' + activeTab.url);

		// Deeplink = URL of the webpage the user was on when they clicked the confirmation prompt.
		// When visiting the clickthrough, if this paramter is present it will send them back to this link afterwards,
		// rather than dumping them on the homepage.
		var deepLink = encodeURIComponent(activeTab.url);

		visitClickthrough({
			tabId: activeTab.id,
			url: Config.gaylUrl + activeTab.activeStore.url + '&dl=' + deepLink,
			source: source,
			closePopup: false
		});
	}

	/**
	 * Invoked when the user has said "no" to the Donation Reminder.
	 */
	onRejection() {
		// Decline the currently active tab
		var activeTab = this.tabs.activeTab;
		console.log('User has rejected the retailer - ' + logObject(activeTab.currentStore));
		activeTab.activeStore = {};
		activeTab.status = StoreStatus.NOT_SUPPORTED;
		this.updateIcon();
		this.hideConfirmationDialog(activeTab.id);

		// Decline any other tabs which contain this retailer
		// todo: combine this with the above and do all tabs in one go
		console.log('Declining all other tabs...');
		this.tabs.updateTabsWithMatchingStore(activeTab);
		var tabs = this.tabs.findAllByStoreId(activeTab.currentStore.id, activeTab.id);
		console.log('Found this store in ' + tabs.length + ' other tabs.');
		this.hideConfirmationDialogForTabs(tabs);
	}

	/**
	 * Updates the extension icon, based on the status of the current tab (i.e. turning it red / green)
	 */
	updateIcon() {
		var activeTab = this.tabs.activeTab;
		switch(activeTab.currentStatus()) {
			// Same icon for these two
			case StoreStatus.BLANK:
			case StoreStatus.NOT_SUPPORTED:
				setBrowserIcon('inactive_', '.png');
				break;
			// Same icon for these two
			case StoreStatus.TRACKING:
			case StoreStatus.TRACKED:
				setBrowserIcon('active_', '.png');
				break;
			default: // Can't handle these
				console.log('Unknown store status: ' + activeTab.currentStatus());
				break;
		}
	}

	/**
	 * Sets the uninstall URL, which will be opened when the extension is uninstalled (except for Safari, which doesn't have this mechanism).
	 */
	setUninstallUrl(url) {
		// Add some additional query string parameters to the uninstall URL, so that we know
		// which browser / version they were using when they uninstalled.
		url = url + '?browser=' + getBrowserName().toLowerCase() + '&version=' + Config.version();
		runtime.setUninstallURL(url);
	}
}

/**
 * 	Helper functions
 */
function isCurrentFrame(frameId) {
	return frameId === 0;
}

/**
 * @return True if the given URL is blacklisted and shouldn't be processed.
 */
function isBlacklisted(url) {
	var re = new RegExp(Config.ignoredUrls, 'g');
	var result = re.test(url);
	return result;
}

/**
 * Used to determine if messages received inside 'onMessage' were actually sent by this extension.
 * This stops messages being sent from other extensions - even though the message handlers themselves do very little and can't really
 * be abused. If the message subject isn't recognised, it won't do anything.
 */
function isTrusted(sender) {
	console.log('isTrusted(' + Config.extensionId() + ', ' + sender.id + ')');

	// First check if the extension ID matches
	if(Config.extensionId() === sender.id) {
		return true;
	}

	// If no match it might be a synthetic event we've created when invoking the onMessage function manually.
	if(sender.id === 'synthetic') {
		return true;
	}

	// Finally check if it's Firefox 48 - 52, none of the above conditions will return true because
	// the sender ID is the extension's internal ID, not the external ID. See this note in the docs
	// about it: https://developer.mozilla.org/en-US/docs/Mozilla/Add-ons/WebExtensions/API/runtime/MessageSender -> id
	// An alternative is setting the extension ID explictly in manfiest.json, but I'm not sure of the implications for newer
	// Firefox versions, since the ID is not explictly defined anywhere, it's all managed through AMO.
	if(getBrowserName() === 'Firefox') {
		return true;
	}

	// If no conditions match it's an untrusted message.
	return false;
}

/**
 * @return True if the given URL is a Chrome new tab URL.
 */
function isGoogleChromeNewTab(url) {
	return url.indexOf('_/chrome') > -1;
}

/**
 * @return True if the given URL has a scheme we don't support / care about, e.g. chrome://, file://, etc.
 */
function isIgnoredScheme(url) {
	for (var i = 0; i < Config.ignoredSchemes.length; i++) {
		var scheme = Config.ignoredSchemes[i];
		if(url.indexOf(scheme) === 0) {
			return true;
		}
	}
	return false;
}

module.exports = ExtensionCore;