/**
 * 	WebExtensions browserAction API polyfill.
 *
 * 	@author Daniel Moffat
 */

var browserAction;

// Firefox / Chrome
if('undefined' !== typeof chrome && chrome.browserAction) {
	browserAction = chrome.browserAction;
}

// Microsoft Edge
if('undefined' !== typeof browser && browser.browserAction) {
	browserAction = browser.browserAction;
}

module.exports = browserAction;