/**
 *	WebExtensions webNavigation polyfill.
 *
 * 	@author Daniel Moffat
 */

var webNavigation;

// Firefox / Chrome
if('undefined' !== typeof chrome && chrome.webNavigation) {
	webNavigation = chrome.webNavigation;
}

// Microsoft Edge
if('undefined' !== typeof browser && browser.webNavigation) {
	webNavigation = browser.webNavigation;
}

module.exports = webNavigation;