/**
 *	WebExtensions windows polyfill.
 *
 * 	@author Daniel Moffat
 */

var windows;

// Firefox / Chrome
if('undefined' !== typeof chrome && chrome.windows) {
	windows = chrome.windows;
}

// Microsoft Edge
if('undefined' !== typeof browser && browser.windows) {
	windows = browser.windows;
}

// Browsers which don't support this (old Safari/Android Firefox). This is required to stop runtime errors being thrown.
if(!windows) {
    windows = {
       onFocusChanged: {
           addListener: function(callback) {}
       }
    };
}

module.exports = windows;