/**
 * 	Provides a convenient way to set the browser icon for multiple image sizes.
 *  Browser icons live in the 'images' folder and you should provide images in the same sizes as the
 *  'ICON_IMAGE_SIZES' array.
 *  
 * 	@author Daniel Moffat
 */

var { runtime, browserAction } = require('../web-ext/index.js');
var getBrowserName = require('./get-browser-name.js');

var ICON_IMAGE_PATH = "images/";
var ICON_IMAGE_SIZES = [16, 20, 25, 30, 32, 40, 48, 64, 128];

/**
 * @param name, the image name, e.g. my-cool-icon
 * @param suffix, the image suffix, e.g. .png
 */
function setBrowserIcon(prefix, suffix) {
	var paths = {};
	prefix = ICON_IMAGE_PATH + prefix;

	ICON_IMAGE_SIZES.forEach(function(size) {
		paths[size] = runtime.getURL(prefix + size + suffix)
	});

	var browserName = getBrowserName();

	if(browserName === 'Edge') {
		// Edge does not seem to support setting multiple paths like Chrome / Firefox.
		// This just plucks out the 40x40 icon and uses that.
		// todo: Add something to browserAction.setIcon polyfil to cater for this.
		browserAction.setIcon({
			path: paths['40']
		});
	} else if(browserName === 'Safari'){
		// Safari doesn't support:
		// - Setting the icon to an absolute path of an extension resource, e.g. safari-extension://your-extension-id/xxxx.png
		// - Setting the icon to a map of image size -> path to resource
		// Because of this limitation we hardcode the 128px resource and use relative image paths
		var relativeIconPath = prefix + '128' + suffix;
		browserAction.setIcon({
			path: relativeIconPath
		});
	} else {
		browserAction.setIcon({
			path: paths
		});
	}
}

module.exports = setBrowserIcon;