/**
 * 	WebExtensions permissions API polyfill.
 *
 * 	@author Daniel Moffat
 */

var permissions;

// Firefox / Chrome
if('undefined' !== typeof chrome && chrome.permissions) {
	permissions = chrome.permissions;
}

// Safari + other old versions of Firefox (<55) which don't support this API yet.
if(!permissions) {
	permissions = {
		contains: function(permissionsToCheck, callback) {
			// Assume we always have permission, otherwise the extension would incorrectly tell the user to change their settings.
			console.log('Using permissions#contains polyfill.');
			callback(true);
		}
	}
}

module.exports = permissions;