/**
 *	BrowserTabs automatically discovers, and keeps track of the browser tabs that are currently open,
 *	as well as keeping the store state for each tab.
 *
 *  It is not completely independent, and is manipulated from extension-core.js when different browser events happen (i.e window focused change,
 *  new tab opened, etc.).
 *
 * 	Note: the state is completely lost when the background page is reloaded, since this state is only stored in
 * 	memory and not in persistent storage.
 *
 *  todo: Refactor this to be self-contained (e.g. containing all the tab event handlers). The only reason it doesn't already is because it needs
 *  to call updateIcon when the tab is being switched, so that would need to be reworked too.
 *
 * 	@author Daniel Moffat
 */
var Config = require('./config.development.js');
var NavigationTracker = require('./navigation-tracker.js');
var StoreStatus = require('./shared/store-status.js');
var isUndefined = require('./shared/util.js').isUndefined;
var tabs = require('./web-ext/tabs.js');

function BrowserTabs() {
	// Stores a reference to the currently active tab
	this.activeTab = {};

	// Map of tabId => BrowserTab
	this.tabs = new Map();

	// Discover all tabs in all windows and add them to our internal map.
	this.discoverAllTabs()
		.then(this.addTabs.bind(this));
}

/**
 * Finds all tabs in all windows.
 * Returns a promise which contains the list of tabs that were found, or an empty list if none were.
 */
BrowserTabs.prototype.discoverAllTabs = function() {
	console.log('Discovering tabs..')
	var self = this;
	return tabs.customQuery({}).then(function(tabs) {
		// Handle an odd edge case where tabs is undefined, according to the docs this should never happen.
		tabs = tabs || [];
		console.log('Discovered', tabs.length, 'tabs.');
		tabs.forEach(function(tab) {
			console.log(`- Tab ID: ${tab.id} (window:${tab.windowId})`);
		});
		return tabs;
	});
};

/**
 * Given a list of tabs, will add them to the internal map of tabs.
 */
BrowserTabs.prototype.addTabs = function(tabs) {
	var tabs = tabs || [];
	var self = this;
	tabs.forEach(function(tab){
		// Add the tab
		self.add(tab.id);

		// Set the currently active tab.
		if(tab.active) {
			self.setActiveTab(tab.id);
		}
	});
};

/**
 * Sets the active tab to the given tab ID.
 *
 * @param tabId The tab ID to set to active. When -1, it means the window that was switched to didn't contain any active tabs (e.g. In a devtools window)
 *
 * Note: This method will create a new tab if the given tab ID doesn't exist, as this method may be called with a non-existent tab ID.
 */
BrowserTabs.prototype.setActiveTab = function(tabId) {
	console.log('setActiveTab(' + tabId + ')');
	if(tabId === -1) { // -1 == no tab
		return;
	}

	var tab = this.find(tabId);

	// No existing tab with that ID, create a new one
	if(!tab) {
		this.activeTab = this.add(tabId)
		return;
	}

	// Existing tab with this ID, set it to active
	this.activeTab = tab;
};

/**
 * @return A list of tab IDs that are stored in the tab map.
 */
BrowserTabs.prototype.ids = function() {
	var ids = [];
	this.tabs.forEach(function(tab, tabId) {
		ids.push(tabId);
	})
	return ids;
};

/**
 * Adds a new tab to the internal map.
 * @return The tab object that was created.
 */
BrowserTabs.prototype.add = function(tabId) {
	this.tabs.set(tabId, new BrowserTab(tabId));
	return this.find(tabId);
};

/**
 * @return A tab with the given ID. Will be null if a tab doesn't exist with that ID
 */
BrowserTabs.prototype.find = function(tabId) {
	var tab = this.tabs.get(tabId);
	return tab;
};

/**
 * Removes the given tab ID from the internal tabs map.
 */
BrowserTabs.prototype.remove = function(tabId) {
	this.tabs.delete(tabId);
};

/**
 * @return true if a tab is active.
 */
BrowserTabs.prototype.hasActiveTab = function() {
	return !isUndefined(this.activeTab.id);
};

/**
 * @return True if the given tab ID matches that of the active tab.
 */
BrowserTabs.prototype.isActiveTab = function(tabId) {
	if(!this.hasActiveTab()) {
		return false;
	}

	return this.activeTab.id === tabId;
};

/**
 * Find the first tab with the given store ID in, excluding the current tab ID that was given.
 *
 * @return The tab object which has the given store ID as the current store.
 */
BrowserTabs.prototype.findByStoreId = function(storeId, currentTabId) {
	var tabs = this.findAllByStoreId(storeId, currentTabId);
	if(tabs.length > 0) {
		return tabs[0];
	}
	return null;
};

/**
 * Finds all tab IDs with the given store ID.
 *
 * @return A list of tab IDs with the given store ID in, excluding the current tab ID that was given.
 */
BrowserTabs.prototype.findAllByStoreId = function(storeId, currentTabId) {
	var found = [];
	this.tabs.forEach(function(tab, tabId) {
		// Exclude the current tab's ID.
		if(tab.currentStore.id === storeId && tabId !== currentTabId) {
			found.push(tab);
		}
	});
	return found;
};

/**
 * Updates the state of all tabs to match the state of the given tab, but only if they're on the same store.
 */
BrowserTabs.prototype.updateTabsWithMatchingStore = function(currentTab) {
	console.log('updateTabsWithMatchingStore()');
	var tabs = this.findAllByStoreId(currentTab.currentStore.id, currentTab.id);
	tabs.forEach(function(tab, tabId) {
		console.log('Changing state of tab ' + tabId);
		tab.status = currentTab.status;
		tab.activeStore = currentTab.activeStore;
	});
};

/**
 * 	Model for a browser tab, used by BrowserTabs.
 */
function BrowserTab(id) {
	// The tab's ID, determined by the browser.
	this.id = id;

	// The URL this tab is currently on.
	this.url = null;

	// The store this tab is currently on. We set this to an empty object so that
	// we can safely access the 'id' property without having to check if this is undefined.
	this.currentStore = {};

	// The store the user last activated on this tab. This may differ from this.currentStore.
	this.activeStore = {};

	// The current tracking status for this.currentStore
	// See store-status.js for a full list of statuses.
	this.status = StoreStatus.BLANK

	// Keeps track of all URLs this tab has navigated through to reach the current URL (includes redirects).
	this.navigation = new NavigationTracker();

	this.pendingProcess = null;

	console.log('BrowserTab(' + id + ') created.');
}

/**
 * Executes the given callback once within a given time.
 */
BrowserTab.prototype.executeOnce = function(callback) {
	if(this.pendingProcess) {
		clearTimeout(this.pendingProcess);
	}
	this.pendingProcess = setTimeout(callback, Config.processingDelayMs);
};

/**
 * Creates a BrowserTab object from a raw object in the shape of a BrowserTab object.
 *
 * Why?
 * ---
 * When sending data from the background page to the popup, all of the methods on this object are lost,
 * so we need to recreate it from scratch.
 */
BrowserTab.prototype.from = function(browserTab) {
	// Background page might not have sent a proper response.
	if(!browserTab || !browserTab.id){
		return null;
	}
	var tab = new BrowserTab(browserTab.id);
	for(var prop in browserTab) {
		tab[prop] = browserTab[prop];
	}
	return tab;
};

/**
 * @return True if the given tab ID has the same ID as this tab.
 */
BrowserTab.prototype.isSameTab = function(tabId) {
	return this.id === tabId;
};

/**
 * @return True if the given store object is the current store for this tab.
 */
BrowserTab.prototype.isCurrentStore = function(store) {
	// No current store means it's the first time a store has been loaded in this tab.
	if(isUndefined(this.currentStore.id)) {
		return false;
	}
	return this.currentStore.id === store.id;
};

/**
 * @return The current status of this tab - possible states are listed in store-status.js
 */
BrowserTab.prototype.currentStatus = function() {
	return this.status;
};

/**
 * @return True if this tab has a store loaded in it.
 */
BrowserTab.prototype.hasStore = function() {
	return !isUndefined(this.currentStore.id);
};

BrowserTab.prototype.toString = function() {
	return `Tab(${this.id}), currentStore=${this.currentStore.name}, activeStore=${this.activeStore.name}, status=${this.status}`;
};

/**
 * Copies specific properties from the source tab to the target tab.
 */
BrowserTab.copy = function(srcTab, targetTab) {
	targetTab.navigation = srcTab.navigation;
	targetTab.status = srcTab.status;
	targetTab.currentStore = srcTab.currentStore;
	targetTab.activeStore = srcTab.activeStore;
};

module.exports = {
	BrowserTabs,
	BrowserTab
};