/**
 * 	WebExtensions tabs API polyfill.
 *
 * 	@author Daniel Moffat
 */

var runtime = require('./runtime.js');

var tabs;

// Firefox / Chrome
if('undefined' !== typeof chrome && chrome.tabs) {
	tabs = chrome.tabs;
}

// Microsoft Edge
if('undefined' !== typeof browser && browser.tabs) {
	tabs = browser.tabs;
}

/**
 *  Implementation of the tabs.get/tabs.query APIs is different between browsers, some take a callback and others return promises.
 *  This normalises those implementations so they work consistently. We use a different property name for these altered methods
 *  as certain browsers (Microsoft Edge) do not allow you to modify the properties.
 */

tabs.customQuery = function customQuery(queryInfo) {
	return new Promise(function(resolve, reject) {
		tabs.query(queryInfo, function(tabs) {
			resolve(tabs);
		});
	});
};

tabs.customGet = function customGet(tabId) {
	return new Promise(function(resolve, reject) {
		tabs.get(tabId, function() {
			if(runtime.lastError) {
				reject();
			} else {
				resolve(tabId);
			}
		});
	});
}

module.exports = tabs;