/**
 * This is responsible for interacting with the GAYL web service.
 *
 * How the GAYL web service works:
 * - Log in / sign up and get a device security code
 * - This code is sent in future requests, it tells the server this user has logged in and
 * can access the API.
 *
 * Each method that requires authentication will have a "deviceSecurityCode" as the first parameter. To get this,
 * you need to either:
 * - Log in using the extension
 * - Be already logged into GAYL.com
 *
 * To see a full list of methods and their request / response formats, you can download Postman (todo: add link to postman here.)
 *
 * @author Daniel Moffat
 *
 */

var URI = require('urijs');
var Request = require('./request.js');
var Config = require('./config.development.js');
var util = require('./shared/util.js');
var Cache = require('./cache.js');

// Default options used for every request.
var DEFAULT_OPTIONS = {
	headers: {
		'Authorization': Config.apiAuthHeader()
	}
};

// The time this script was loaded
var created = util.getTimestamp();

// Temporary caches for API responses.
// These are really handy for endpoints which are called multiple times per website to reduce the amount of overall calls that are made.
var findStoreByUrlCache = new Cache(1000 * 60 * 3); // 3 minutes
var findStoreByUrlAnonymousCache = new Cache(1000 * 60 * 3); // 3 minutes
var findSimilarStoresCache = new Cache(60000); // 1 minute
var urlBlacklistCache = new Cache(1000 * 60 * 60); // 1 hour
var adsCache = new Cache(1000 * 60 * 60); // 1 hour
var serpRulesCache = new Cache(1000 * 60 * 60); // 1 hour
var userStateCache = new Cache(1000 * 60); // 1 minute

var GaylApiClient = {
	// Logs the user into the extension
	login: function(email, password) {
		var endpoint = Config.apiBase + 'user/find';
		var payload = {
			email: email,
			password: password
		};
		return Request.post(endpoint, payload, DEFAULT_OPTIONS);
	},

	// Logs the user into the GAYL website
	cookieLogin: function(deviceSecurityCode) {
		var endpoint = Config.apiBase + 'extension/cookieLogin';
		var payload = { deviceSecurityCode };
		return Request.post(endpoint, payload, DEFAULT_OPTIONS);
	},

	// Logs the user out of the GAYL website
	cookieLogout: function() {
		var endpoint = Config.apiBase + 'extension/cookieLogout';
		return Request.post(endpoint, {}, DEFAULT_OPTIONS);
	},

	// Returns the current user's state on the GAYL website
	// Options is an object with the following structure:
	// {
	// 		// Whether to retrieve a response from the cache or not.
	//		'useCache': true
	// }
	getState: function(options) {
		options = options || {};

		// Add the time to the query param, since we don't want the browser to cache this request.
		var endpoint = Config.apiBase + 'user/state?cb=' + util.getTimestamp();

		var cachedResponse = userStateCache.get('userState');
		if(cachedResponse && options.useCache) {
			return Promise.resolve(cachedResponse);
		}

		return Request.get(endpoint, DEFAULT_OPTIONS).then(function(resp) {
			userStateCache.set('userState', resp);
			return resp;
		});
	},

	getSerpRules: function() {
		var endpoint = Config.apiBase + 'serp/rules?cb=' + util.getDayAndYear();
/*
		var cachedResponse = serpRulesCache.get('serpRules');
		if(cachedResponse) {
			return Promise.resolve(cachedResponse);
		}
*/
		// Add the day/year to the query param, since we want the browser to cache this request, but only for 1 day maximum, since
		// the rules might change, but they won't change often enough for this to be cached less.
		return Request.get(endpoint, DEFAULT_OPTIONS).then(function(resp) {
			serpRulesCache.set('serpRules', resp.rules);
			return resp.rules;
		});
	},

	// Lists a user's favourite stores followed by popular stores.
	listUserStores: function(deviceSecurityCode) {
		const endpoint = `${Config.apiBaseV2}user/stores`;
		const payload = { deviceSecurityCode };
		return Request.post(endpoint, payload, DEFAULT_OPTIONS).then(resp => {
			return resp.stores;
		});
	},

	// List this user's popular stores
	listPopularStores: function(deviceSecurityCode) {
		var endpoint = Config.apiBase + 'store/popular?includeFavourites=true';
		var payload = { deviceSecurityCode };
		return Request.post(endpoint, payload, DEFAULT_OPTIONS).then(function(resp) {
			// Return only the field we need, otherwise client code looks weird, e.g.
			// listPopularStores(...).then(resp => console.log(resp.popularStores))
			// vs
			// listPopularStores(...).then(popularStores => console.log(popularStores));
			return resp.popularStores;
		});
	},

	// List account options for the account area
	listAccountOptions: function(deviceSecurityCode) {
		var endpoint = Config.apiBase + 'user/listAccountOptions';
		var payload = { deviceSecurityCode };
		return Request.post(endpoint, payload, DEFAULT_OPTIONS).then(function(resp) {
			// Filter out app-specific account options
			// todo: add an additional parameter to this endpoint, to only return account options for the
			// extension.
			var filtered = [];
			for (var i = 0; i < resp.accountOptions.length; i++) {
				var option = resp.accountOptions[i];
				if(option.name !== 'Review app') {
					filtered.push(option);
				}
			}
			return filtered;
		});
	},

	// Lists a user's favourite stores
	listFavouriteStores: function(deviceSecurityCode) {
		var endpoint = Config.apiBase + 'favourite/list';
		var payload = { deviceSecurityCode };
		return Request.post(endpoint, payload, DEFAULT_OPTIONS).then(function(resp) {
			return resp.stores;
		});
	},

	// Add a favourite store
	addFavouriteStore: function(deviceSecurityCode, storeId) {
		var endpoint = Config.apiBase + 'favourite/add';
		var payload = { deviceSecurityCode, storeId };
		return Request.post(endpoint, payload, DEFAULT_OPTIONS);
	},

	// Remove a favourite store
	removeFavouriteStore: function(deviceSecurityCode, storeId) {
		var endpoint = Config.apiBase + 'favourite/remove';
		var payload = { deviceSecurityCode, storeId };
		return Request.post(endpoint, payload, DEFAULT_OPTIONS);
	},

	// Find a store by query.
	findStoreByQuery: function(deviceSecurityCode, query) {
		var endpoint = Config.apiBase + 'store/findByQuery?includeFavourites=true';
		var payload = { deviceSecurityCode, query };
		return Request.post(endpoint, payload, DEFAULT_OPTIONS).then(function(resp) {
			return resp.stores;
		});
	},

	// Find a store by URL, returns the following:
	// {
	// 		found: boolean,
	// 		standDown: boolean,
	// 		list: [
	// 			..stores..
	// 		]
	// }
	findStoreByUrl: function(deviceSecurityCode, urls) {
		var endpoint = Config.apiBase + 'store/findByUrl';
		var lastUrl = util.getLastItem(urls);

		if(!lastUrl) {
			console.warn('WARN: No URL found in the URL list.');
			return Promise.reject(util.createError('no_navigated_urls'));
		}

		// Parse the URI so we can access the domain / host
		var url = URI(lastUrl);

		// Check the cache first
		var cachedResponse = findStoreByUrlCache.get(url.host());
		if(cachedResponse) {
			return Promise.resolve(cachedResponse);
		}

		// Nothing found in the cache.
		var payload = {
			deviceSecurityCode,
			url: urls,
			domain: url.domain(),
			host: url.host(),
			searchType: 'I'
		};

		return Request.post(endpoint, payload, DEFAULT_OPTIONS).then(function(resp) {
			findStoreByUrlCache.set(url.host(), resp);
			return resp;
		});
	},

	// Same as the above method, but for anonymous users only.
	findStoreByUrlAnonymous: function(urls) {
		var endpoint = Config.apiBase + 'store/findByUrlAnonymous?version=' + Config.version();
		var lastUrl = util.getLastItem(urls);

		if(!lastUrl) {
			console.warn('WARN: No URL found in the URL list.');
			return Promise.reject(util.createError('no_navigated_urls'));
		}

		var url = URI(lastUrl);

		// Check the cache first
		var cachedResponse = findStoreByUrlAnonymousCache.get(url.host());
		if(cachedResponse) {
			return Promise.resolve(cachedResponse);
		}

		// Nothing found in the cache.
		var payload = {
			url: urls,
			domain: url.domain(),
			host: url.host(),
			searchType: 'A'
		};

		return Request.post(endpoint, payload, DEFAULT_OPTIONS).then(function(resp) {
			findStoreByUrlAnonymousCache.set(url.host(), resp);
			return resp;
		});
	},

	// Find similar stores to the given store.
	findSimilarStores: function(deviceSecurityCode, storeId) {
		var endpoint = Config.apiBase + 'stores/similar?includeFavourites=true';
		var payload = { deviceSecurityCode, storeId };

		var cachedResponse = findSimilarStoresCache.get(storeId);
		if(cachedResponse) {
			return Promise.resolve(cachedResponse);
		}

		return Request.post(endpoint, payload, DEFAULT_OPTIONS).then(function(resp) {
			findSimilarStoresCache.set(storeId, resp);
			return resp;
		});
	},

	// Retrieve the URL blacklist, which specifies URLs which should be ignored when processing
	// URLs.
	getUrlBlacklist: function() {
		var endpoint = Config.apiBase + 'urlBlacklist/';

		var cachedResponse = urlBlacklistCache.get('blacklist');
		if(cachedResponse) {
			return Promise.resolve(cachedResponse);
		}

		return Request.get(endpoint, DEFAULT_OPTIONS).then(function(resp) {
			urlBlacklistCache.set('blacklist', resp);
			return resp
		});
	},

	// Get ads that will be displayed within the extension. These are paid-for placements from retailers
	// we work with.
	getAds: function() {
		var endpoint = Config.apiBase + 'urlBlacklist/';

		var cachedResponse = adsCache.get('ads');
		if(cachedResponse) {
			return Promise.resolve(cachedResponse);
		}

		return Request.get(endpoint, DEFAULT_OPTIONS).then(function(resp) {
			adsCache.set('ads', resp.ads);
			return resp.ads;
		});
	},

	// Check the given list of URLs to see if they are supported on our website. Used for search engine result pages only (SERP).
	checkSerpUrls: function(urls) {
		var endpoint = Config.gaylUrl + '/toolbar/find?version=' + Config.version();
		return Request.post(endpoint, urls, {headers: {}});
	},

	// Fetches the refer a friend metadata, this includes things such as the text to be shared and the referral URL.
	getReferAFriendMetaData: function() {
		var endpoint = `${Config.apiBaseV2}refer/get-metadata`;
		return Request.get(endpoint, DEFAULT_OPTIONS);
	}
}

module.exports = GaylApiClient;