/**
 *	WebExtensions webRequest polyfill.
 *
 * 	@author Daniel Moffat
 */

var webRequest;

// Firefox / Chrome
if('undefined' !== typeof chrome && chrome.webRequest) {
	webRequest = chrome.webRequest;
}

// Microsoft Edge
if('undefined' !== typeof browser && browser.webRequest) {
	webRequest = browser.webRequest;
}

module.exports = webRequest;