/**
 * This is responsible for storing persistent data. It's basically just a wrapper
 * around the underlying WebExtensions 'storage' API that uses promises
 *
 * Uses promises rather than callbacks to make client code easier to follow.
 *
 * Currently used keys:
 * - deviceSecurityCode, token that tells the back end this user has logged in already
 * - cause, information about the currently logged in user's cause.
 * - user, information about the currently logged in user.
 * - popularStores, contains a list of popular stores
 * - accountOptions, contains a list of available account options
 * - favouriteStores, contains a list of favourite stores for the currently logged in user
 *
 * todo: Need to see what happens when the quota for storage has been hit - we're not storing huge amounts of information
 * currently (the data is a fixed size and there is a certain count of them) but could be a problem if we start collecting more stuff.
 *
 * @author Daniel Moffat
 */

// The underlying storage mechanism, this differs based on the browser.
var storageProvider = require('./web-ext/storage.js').local;

// When errors occur, they are set here
var lastError = require('./web-ext/runtime.js').lastError;

var Storage = {
	/**
	 * 	Retrieve a single key / multiple keys from the storage.
	 *  @param keys The keys (array) or key (string) to get.
	 *  @return A map of key => data.
	 */
	get: function(keys) {
		return new Promise(function(resolve, reject) {
			storageProvider.get(keys, function(items) {
				if(lastError || !items) {
					reject(lastError);
				} else {
					resolve(items)
				}
			});
		});
	},

	/**
	 * 	Gets every key from the storage.
	 *  @return A map of key => data.
	 */
	getAll: function() {
		return this.get(null);
	},

	/**
	 *	Get a single key (does not support multiple) from the storage. If 'key' doesn't exist, 'loadingFn' will be invoked and its result
	 *	will be saved in the storage.
	 *
	 * 	@param loadingFn A function that returns a Promise (https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Promise)
	 * 	@param key The key to get from storage
	 * 	@return The data for key
	 *
	 *  todo: check loadingFn is a function + returns a promise
	 *
	 */
	getOrElse: function(key, loadingFn) {
		var self = this;
		return new Promise(function(resolve, reject) {
			// Attempt to get the key
			self.get([key]).then(function(data) {

				// No key found, call loading function + add to storage
				if(!data[key]) {
					return loadingFn().then(function(result) {
						self.setSingle(key, result).then(function(data) {
							resolve(data[key]);
						});
					}).catch(function(err) { // Propagate the error
						reject(err);
					});
				} else {
					// Key found, just return the data
					resolve(data[key]);
				}
			});
		});
	},

	/**
	 * 	Set multiple values in the storage.
	 *
	 * 	@param map A map of key to store as => data to store.
	 * 	@return The persisted data, in the same format it came in with.
	 */
	set: function(map) {
		return new Promise(function(resolve, reject) {
			storageProvider.set(map, function() {
				lastError ? reject(lastError) : resolve(map);
			});
		});
	},

	/**
	 * Set a single value in the storage.
	 * @param key   The key to store
	 * @param value The data to store.
	 * @return A map with a single key and data.
	 */
	setSingle: function(key, value) {
		// Why create an object? You can't have dynamic property names in an object literal
		var dataToSet = {};
		dataToSet[key] = value;
		return this.set(dataToSet);
	},

	/**
	 * 	Removes the given keys from the storage.
	 * 	@param keys An array of keys to delete, or a single string key.
	 */
	remove: function(keys) {
		return new Promise(function(resolve, reject) {
			storageProvider.remove(keys, function() {
				lastError ? reject(lastError) : resolve();
			});
		});
	},

	/**
	 * 	Removes ALL keys from storage.
	 */
	clear: function() {
		return new Promise(function(resolve, reject) {
			storageProvider.clear(function() {
				lastError ? reject(lastError) : resolve();
			});
		});
	}
}

module.exports = Storage;