/**
 * 	WebExtensions storage API polyfill.
 *
 * 	@author Daniel Moffat
 */

var isArray = require('../shared/util.js').isArray;

var storage;

// Firefox / Chrome
if('undefined' !== typeof chrome && chrome.storage) {
	storage = chrome.storage;
}

// Microsoft Edge
if('undefined' !== typeof browser && browser.storage) {
	storage = browser.storage;
}

module.exports = storage;