/**
 * 	Provides a cross-browser way to open a new tab. Uses browser-specific APIs where possible.
 *
 *  If you're looking to update the URL in the current tab, look at web-ext/tabs.js
 *
 * 	@author Daniel Moffat
 */

var tabs = require('../web-ext/tabs.js');

function openTab(url) {
	console.log('Opening tab with URL: ' + url);
	// Safari
	if(typeof safari !== 'undefined') {
		// If we don't close the popup before opening a new tab, the popup will get stuck
		// and will be permanently fixed on top of the window.
		require("./close-popup.js")();
		safari.application.activeBrowserWindow.openTab().url = url;
	} else {
		// Chrome / Firefox / Edge
		tabs.create({
			url: url
		});
	}
}

module.exports = openTab;