var config = require('./config.development.js');
var runtime = require('./web-ext/runtime.js');
var cookies = require('./web-ext/cookies.js');

class NotificationListener {

	constructor() {

        // Establish connection with app
        let  port = runtime.connectNative("DonationReminderNotification");
        port.onMessage.addListener(response => {

            // todo: we don't have the 'cookies' permission in browsers, we need to a way to physically remove
            //       code when building for web/android for this to be usable.
             cookies.set({
                url: config.gaylUrl,
                domain: config.gaylDomain,
                name: "android_notification",
                value:  response.token,
                expirationDate: 2147483647,
                path: '/'
              });

        });

	}

}

module.exports = NotificationListener;