/**
 * 	Main extension script.
 *
 *  It's instantiated ONCE when the extension is loaded (exactly when it's loaded is determined by the browser) and then sits in the background
 *  listening for events.
 *
 *  Note: Ideally this would be non-persistent, but webRequest doesn't support non-persistent background pages yet.
 *
 * 	@author Daniel Moffat
 */
var ExtensionCore = require('./lib/extension-core.js');
var InstallationListener = require('./lib/installation-listener.js');
var NotificationListener = require('./lib/notification-listener.js');

var { isAndroidApp } = require('./lib/shared/util.js');

if(isAndroidApp()) {
    // This connects to our Android app to get the FCM registration token so we can send it to our backend.
    new NotificationListener();
} else {
    // React to extension installation / updates - not applicable to Android as that controls it's own update process.
    new InstallationListener();
}

// Intialise the extension, which contains all of the logic.
// We also export it so its methods can be accessed directly by the popup component.
window.gaylExtension = new ExtensionCore();