/**
 * 	WebExtensions API polyfill(s).
 *
 *	These basically normalise the behaviour of WebExtensions APIs across all browsers.
 *
 * 	They can be require'd and will "just work" on all supported browsers, no need to worry about cross-browser
 * 	compatibility. You can either import the specific APIs you need, or the whole lot at once by including this file.
 *
 *  Note: This only polyfills methods / properties that are actually used by the extension, not all of them.
 */
module.exports = {
	browserAction: require('./browser-action.js'),
	cookies: require('./cookies.js'),
	tabs: require('./tabs.js'),
	permissions: require('./permissions.js'),
	runtime: require('./runtime.js'),
	storage: require('./storage.js'),
	webRequest: require('./web-request.js'),
	webNavigation: require('./web-navigation.js'),
	windows: require("./windows.js")
};