/**
 * Listens for browser extension installation / update events.
 *
 * @author Daniel Moffat
 */

var getBrowserName = require('./shared/get-browser-name.js');
var getMobileOs = require('./shared/get-mobile-os.js');
var Config = require('./config.development.js');
var openTab = require('./shared/open-tab.js');
var { runtime } = require('./web-ext/index.js');
var { getGaylUrl, isAndroidApp, isMajorVersionUpdate } = require('./shared/util.js');

class InstallationListener {
	constructor() {
		/**
		 * 	Invoked when:
		 * 	- Extension is first installed
		 * 	- Extension is updated to a new version
		 * 	- Browser is updated to a new version
		 *
		 * 	docs: https://developer.mozilla.org/en-US/Add-ons/WebExtensions/API/runtime/onInstalled
		 *
		 * 	NOTE: runtime.onInstalled doesn't seem to be present in Firefox 48 - 52. So we need to check for its existence first.
		 */

		if(isAndroidApp())
		    return;

		if(runtime.onInstalled) {
			runtime.onInstalled.addListener(this.onExtensionInstalled.bind(this));
		}
	}

	onExtensionInstalled(details) {
		switch(details.reason) {
			case 'install':
				this.onExtensionInstalledFirstTime(details);
				break;
			case 'update':
				this.onExtensionUpdated(details);
				break;
			// These don't require us to do anything, pass through
			case 'chrome_update':
			case 'shared_module_update':
			default:
				break;
		}
	}

	onExtensionInstalledFirstTime(details) {
		var welcomeUrl = getGaylUrl('/welcome');
		var installedVersion = Config.version();
		console.log('Extension installed for the first time.', installedVersion);
		openTab(welcomeUrl + "?version=" + installedVersion);
	}

	onExtensionUpdated(details) {
		var updateUrl = getGaylUrl('/update');
		var newVersion = Config.version();
		var previousVersion = details.previousVersion;

		console.log('The extension has been updated: ' + previousVersion + ' -> ' + newVersion);

		// Open the update webpage for major version updates.
		if(isMajorVersionUpdate(previousVersion, newVersion)) {
			console.log('Extension has updated major versions.');
			openTab(updateUrl + "?previousVersion=" + previousVersion + '&newVersion=' + newVersion);
		}
	}
}

module.exports = InstallationListener;