/**
 * Responsible for making HTTP requests in a browser-agnostic manner (uses XMLHttpRequest internally).
 *
 * All request methods (get, post) return Promise objects (https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Promise),
 * to make it easier to handle responses (similar to jQuery.ajax).
 *
 * Options:
 * - dataType, The type of data you're receiving, defaults to JSON, supported values are:
 * 	- 'json'
 * - headers, map of header names / values
 * - timeout, the amount of time (in ms) to wait before the request times out.
 *
 * Example:
 *
 * // GET request
 * Request.get('/path/to/resource')
 * 		  .then(resp => console.log(resp))
 * 		  .catch(err => {
		   		console.log(err.statusCode, err.message);
		   });

   todo: Try and find a lightweight HTTP client which performs similar tasks. This is just reinventing the wheel, and not very well.
 *
 * @author Daniel Moffat
 */

var Messages = require('./shared/messages.js');
var Config = require('./config.development.js');

// HTTP status codes
var STATUS_OK = 200;

var Request = {
	get: function (url, options) {
		return fetch('GET', url, {}, options || {});
	},

	post: function(url, data, options) {
		return fetch('POST', url, data || {}, options || {});
	}
}

/** This function does the actual AJAX requests for both POST / GET requests */
function fetch(type, url, data, options) {
	var data = data || {};
	var type = type || 'GET';
	var options = options || {};
	var requestStart = 0;
	return new Promise(function(resolve, reject) {
		requestStart = Date.now();
		var req = new XMLHttpRequest();
		req.onreadystatechange = function() {
			handleRequest(url, req, resolve, reject, options, requestStart);
		};
		req.open(type, url);
		req.timeout =  options.timeout || Config.requestTimeoutMs;
		addHeaders(req, options.headers);
		if(req.type === 'GET') {
			req.send();
		} else {
			req.send(JSON.stringify(data));
		}
	});
}

/** Add request headers to the given request */
function addHeaders(req, headers) {
	if(!headers) return;
	for(var header in headers) {
		req.setRequestHeader(header, headers[header]);
	}
	req.setRequestHeader('Content-Type', 'application/json; charset=UTF-8');
}

/**
 * Invoked each time XMLHttpRequest.onreadstatechange is triggered, used by fetch.
 */
function handleRequest(url, req, resolve, reject, options, requestStart) {
	if(req.readyState === XMLHttpRequest.DONE) {
		// console.log(`Request (status:${req.status}) '${url}' took ${Date.now() - requestStart}ms`);

		// 200
		if(req.status === STATUS_OK) {
			return handleStatusOk(req, resolve, reject, options);
		}

		// 4xx, 5xx and 0 (XMLHttpRequest)
		return handleStatusNotOk(req, resolve, reject);
	}
}

// Invoked for the OK HTTP response (200)
function handleStatusOk(req, resolve, reject, options) {
	// TODO: need to add handling for different datatypes. Currently if a dataType is specified, it will just resolve with the content.
	if(options.dataType && options.dataType !== 'json') {
		resolve(req.responseText);
	} else {
		try {
			resolve(JSON.parse(req.responseText));
		} catch(e) { // If we failed to parse the JSON, just return the response.
			resolve(req.responseText);
		}
	}
}

// Invoked for every HTTP response except OK (200)
function handleStatusNotOk(req, resolve, reject) {
	// The status will be 0 if:
	// - The request timed out
	// - The browser cancelled the request
	// I cannot use ontimeout, as that is invoked AFTER handleRequest is invoked. So by the time the ontimeout
	// callback is fired, the promise has already been rejected.
	if(req.status === 0) {
		reject({
			statusCode: req.status,
			message: 'Request timed out.',
			type: 'http_error_timeout_or_cancelled'
		});
	} else {
		// If it's not 0, it will be 4xx or 5xx, which we can handle.
		reject({
			statusCode: req.status,
			message: tryParseErrorMessageFromResponse(req),
			type: 'api_error'
		});
	}
}

function tryParseErrorMessageFromResponse(req) {
	var errorMessage = Messages.GENERIC_ERROR_MESSAGE;
	try {
		// First, try parse the response as JSON. Our API returns all error responses as JSON.
		var resp = JSON.parse(req.responseText);
		var errors = resp.errors;
		if(errors && errors.length > 0) {
			errorMessage = Messages.API_ERROR_MESSAGES[errors[0].error_code]
		}
	} catch(e) {
		// If this happened, our API did not return JSON, I've never actually seen this happen,
		// but it should be covered.
		errorMessage = Messages.REQUEST_ERROR_MESSAGES[req.status];
		if(!errorMessage) {
			console.warn('No message found for status code', req.status);
		}
	}
	return errorMessage;
}

module.exports = Request;