/**
 * 	Provides a cross-browser way to close the Donation reminder popup window.
 *
 * 	@author Daniel Moffat
 */

function closePopup() {
	if(typeof safari !== 'undefined') {
		safari.extension.popovers[0].hide();
	} else {
		// In Safari, this will destroy the window, causing it to remain unresponsive until you force quit.
		window.close();
	}
}

module.exports = closePopup;