/**
* Checks if the extension is allowed access to all URLs. If the callback is invoked with false, it means the user has changed
* their host runtime permissions (https://developer.chrome.com/extensions/runtime_host_permissions) and the extension can't operate.
*
* @param callback Will be invoked with either true|false, depending on permissions.
*
* @author Daniel Moffat
*/

var permissions = require('../web-ext/permissions.js')

 function checkPermissions(callback) {
 	var permissionsToCheck = {
 		origins: ['http://*/*', 'https://*/*']
 	};
 	permissions.contains(permissionsToCheck, function(hasPermission) {
 		if(callback) { // Handle being called without a callback
 			callback(hasPermission);
 		}
 	}.bind(this));
 }

module.exports = checkPermissions;