/**
 * Utility for visiting clickthrough URLs.
 *
 * This is invoked any time a user clicks a clickthrough URL. It does the following things:
 * - Appends a 'check_adblocker' parameter to the URL.
 * - Appends an 'extension_source' parameter to the URL.
 *
 * @author Daniel Moffat
 *
 * todo: The parameters should be appended on the server when they are generated, this is a workaround that
 *       shouldn't really exist - me after looking 2 years later.
 */

var Config = require('../config.development.js');
var visitUrl = require('./visit-url.js');

/**
 * Follows the URL specified in the options, in the tab specified in the options.
 * Options look like this:
 *  {
 *  	// URL to follow
		"url": "http://www.google.com",
		// Tab ID to open this URL in
		"tabId": "1234",
		// Where this was invoked from
		"source": "RETAILER",
		// Whether to close the extension popup after following the link
		"closePopup": true
 	}
 */
function visitClickthrough(options) {
	var tabId = options.tabId;
	var url = options.url;
	var source = options.source || null;
	var closePopup = options.closePopup || false;

	if(!url || !tabId) {
		console.error('visitClickthrough requires a tab ID / URL.');
		return;
	}

	// Prompt the clickthrough to check for adblocking software.
	url += '&check_adblocker=true';

	// Add the version
	url += '&v=' + Config.version();

	// Let the clickthrough know where the click came from
	if(source) {
		url += '&extension_source=' + source;
	} else {
		console.warn('No source found for clickthrough URL.');
	}

	console.log('Final click URL: ' + url);

	visitUrl({ tabId, url, closePopup });
}

module.exports = visitClickthrough;