/**
 * Provides an temporary cache whose entries expire after a fixed timeout. Once the object is
 * destroyed all cache entries will be lost.
 *
 * @author Daniel Moffat
 */

var {isString, getTimestamp} = require('./shared/util.js');

var DEFAULT_CACHE_TIMEOUT = 10000;

function Cache(expiryTimeMs) {
	this.expiryTimeMs = expiryTimeMs || DEFAULT_CACHE_TIMEOUT;
	this.entries = {};
}

/**
 * Get an entry from the cache.
 *
 * @return The value for the given key, or null if the value cannot be found / has expired.
 */
Cache.prototype.get = function(key) {
	var entry = this.entries[key];

	if(!entry) {
		return null;
	}

	var age = getTimestamp() - entry._added;
	if(age >= this.expiryTimeMs) {
		this.remove(key);
		return null;
	}

	return entry;
}

/**
 * Adds an entry to the cache. Does not currently support strings as values, since you
 * can't set properties on them.
 * todo: Make this support strings, would need to wrap the provided value in an object
 * and set the 'added' property.
 */
Cache.prototype.set = function(key, value) {
	if(!key) {
		throw new Error('Cache.set called without a key.');
	}

	if(!value) {
		throw new Error('Cache.set called without a value.');
	}

	if(isString(value)) {
		console.log(value);
		throw new Error('Cache.set doesn\'t support strings as values.');
	}

	// Before adding to the cache, add a timestamp so we know how long it's been there for.
	value._added = getTimestamp();
	this.entries[key] = value;
}

Cache.prototype.remove = function(key) {
	delete this.entries[key];
}

module.exports = Cache;