/**
 * 	Provides a cross-browser way to visit a URL from the Donation reminder popup window.
 *
 * 	@author Daniel Moffat
 */

var tabs = require('../web-ext/tabs.js');
var closePopup = require('./close-popup.js');

/**
 * 	Options looks like this:
 * 	{
 * 		// Whether to close the popup window after visiting the URL
		"closePopup": false,
		// The tab ID to visit the URL in
		"tabId": 1234,
		// The URL to visit
		"url": "http://www.google.com"
 	}
 */
function visitUrl(options) {
	var shouldClosePopup = options.closePopup || false;
	var tabId = options.tabId || null;
	var url = options.url || null;

	if(!url || !tabId) {
		console.error('No URL or tabId found.');
	}

	console.log('visitUrl(' + tabId + ', ' + url + ')');
	tabs.update(tabId, {
		url: url
	});

	if(shouldClosePopup) {
		closePopup();
	}
}

module.exports = visitUrl;